import { Component, Input, OnInit } from '@angular/core';



import { Evidence } from '../../../../@core/data/entities/evidence';

@Component({
  selector: 'ngx-security-cameras',
  styleUrls: ['./security-cameras.component.scss'],
  templateUrl: './security-cameras.component.html',
})
export class SecurityCamerasComponent implements OnInit{
  @Input() childMessage: Evidence; 
   
  evidencesScreenShots: ScreenShot[] = [];

  selectedCamera: any = this.evidencesScreenShots[0];

  userMenu = [{
    title: 'Profile',
  }, {
    title: 'Log out',
  }];

  isSingleView = false;

  ngOnInit()
  {

    
    let src:string;

    this.childMessage.screenshots.forEach(obj=>{
      
      let screenshot:ScreenShot= new ScreenShot;
      screenshot.title="Evidence";
      screenshot.src='data:image/png;base64,' + obj;
      this.evidencesScreenShots.push(screenshot);
    }
    )
  }
  selectCamera(camera: any) {
    this.selectedCamera = camera;
    this.isSingleView = true;
  }
}
class ScreenShot{
  title:string;
  src:string;
}



