import { Component } from '@angular/core';
import { LocalDataSource } from 'ng2-smart-table';
import { ButtonViewComponent } from './button-render.component';
import { SmartTableService } from '../../../@core/data/smart-table.service';
import { Column } from 'ng2-smart-table/lib/data-set/column';




import { EvidenceService } from '../../../@core/data/services/evidence.service';


import { SearchCriteria } from '../../../@core/data/entities/searchCriteria';



import { Evidence } from '../../../@core/data/entities/evidence';


@Component({
  selector: 'ngx-smart-table',
  templateUrl: './evidence-table.component.html',
  styles: [`
    nb-card {
      transform: translate3d(0, 0, 0);
    }
  `],
})
export class SmartTableComponent {

  settings = {
    add: {
      addButtonContent: '<i class="nb-plus"></i>',
      createButtonContent: '<i class="nb-checkmark"></i>',
      cancelButtonContent: '<i class="nb-close"></i>',
    },
    edit: {
      editButtonContent: '<i class="nb-edit"></i>',
      saveButtonContent: '<i class="nb-checkmark"></i>',
      cancelButtonContent: '<i class="nb-close"></i>',
    },
    delete: {
      deleteButtonContent: '<i class="nb-trash"></i>',
      confirmDelete: true,
    },
    actions: false,
    columns: {
      transactionReferenceNo: {
        title: 'Transaction reference No',
        type: 'string',
      },
      userId: {
        title: 'User Id',
        type: 'string',
      },
      machineName: {
        title: 'Machine Name',
        type: 'string',
      },
      capturedDatetime: {
        title: 'Capture Date(mm-dd-yyy)',
        type: 'string',
      },
      noOfEvidences: {
        title: 'No of Evidences',
        type: 'string',
      },
      details:
      {
       title:'Details',
        type: 'custom',
        renderComponent:ButtonViewComponent,
        filter:false,
        //defaultValue: ,
      }
     
    },
  };

  source: LocalDataSource = new LocalDataSource();
  evidences:Evidence[];
  
  constructor(private service: SmartTableService,
  private evidenceService:EvidenceService) {
    const data = this.service.getData();
    


    this.evidenceService.fetchEvidences({} as any)
    .subscribe(x=>{
      console.log(x);
      this.source.load(x.items);
    });
    //console.log(this.evidences);
    //this.source.load(this.evidences);
  }

  onDeleteConfirm(event): void {
    if (window.confirm('Are you sure you want to delete?')) {
      event.confirm.resolve();
    } else {
      event.confirm.reject();
    }
  }
}



