import { LocalDataSource } from 'ng2-smart-table';
import { SmartTableService } from '../../../@core/data/smart-table.service';
import { Column } from 'ng2-smart-table/lib/data-set/column';
import { Component, OnInit, Input } from '@angular/core';
import { ViewCell } from 'ng2-smart-table';
import { ActivatedRoute, Router } from '@angular/router';



import { EvidenceService } from '../../../@core/data/services/evidence.service';



import { Evidence } from '../../../@core/data/entities/evidence';



import { SecurityCamerasComponent } from './security-cameras/security-cameras.component'
import { Image } from 'angular-modal-gallery';
import { NbSearchService } from '@nebular/theme';

@Component({
  selector:'evidence-details',
  templateUrl: './evidence-details.component.html',
  styleUrls: ['./evidence-details.component.scss']
})
export class DetailsComponent implements OnInit {

  public renderValue;
   eviedence : Evidence;

  evidenceList: Evidence[] =[];

  value :any;

  constructor(private _route: ActivatedRoute,
  private evidenceService:EvidenceService,

  private router:Router,
  private searchService: NbSearchService,) { 
    
   }

  ngOnInit() {
    
    this.renderValue = this.value;
    let id = localStorage.getItem('transactionreference');
    this.evidenceService.getEvidence(id).subscribe(x=>{
      console.log(x);
 
       this.evidenceList=x;
      
 
       this.evidenceList.forEach(o=>{
 
         var tempImages: Image[]=[];
         var count=0;

         o.screenshots.forEach(obj=>{     
           
           var img = new Image(
             count++,
              {
                img:'data:image/png;base64,' + obj,
              })
              tempImages.push(img);
         })
         o.images=tempImages;
        });
       })



      
  }

  example() {
    alert(this.renderValue);
  }

}



