import { NgModule } from '@angular/core';
import { Ng2SmartTableModule } from 'ng2-smart-table';


import { ThemeModule } from '../../@theme/theme.module';
import { TablesRoutingModule, routedComponents } from './tables-routing.module';
import { SmartTableService } from '../../@core/data/smart-table.service';
import { DetailsComponent } from './evidence-details/evidence-details.component';
import { EvidenceService } from '../../@core/data/services/evidence.service';
import { ButtonViewComponent } from './evidence-table/button-render.component';
import { SecurityCamerasComponent } from './evidence-details/security-cameras/security-cameras.component';

import 'hammerjs'; // <------ mandatory dependency for angular-modal-gallery
import 'mousetrap'; // <------ mandatory dependency for angular-modal-gallery
import { ModalGalleryModule } from 'angular-modal-gallery'; // 
import { TranslateModule, TranslateLoader } from '@ngx-translate/core';
import { HttpClient } from '@angular/common/http';
import { TranslateHttpLoader } from '@ngx-translate/http-loader';



@NgModule({
  imports: [
    ThemeModule,
    TablesRoutingModule,
    Ng2SmartTableModule,
    ModalGalleryModule,
    TranslateModule.forRoot({loader: {
      provide: TranslateLoader,
      useFactory: HttpLoaderFactory,
      deps: [HttpClient],
    
    
    }})
    
  ],
  declarations: [
    ...routedComponents,
    ButtonViewComponent,
    SecurityCamerasComponent
   
    
    
  ],
  entryComponents: [
    ButtonViewComponent
    ],
  providers: [
    SmartTableService,
    EvidenceService
  ],
})
export class TablesModule { }

// AoT requires an exported function for factories
export function HttpLoaderFactory(http: HttpClient) {
  return new TranslateHttpLoader(http);
}


